.search-result {
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 10px;
  text-decoration: none;
  color: #000;
}

.search-form {
  display: flex;
  margin-bottom: 20px;
}
.search-form button {
  margin-left: 10px;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}
